<template>
<nav class="nav-extended">
    <div class="nav-wrapper orange">
        <a href="#/" class="brand-logo center white-text" style="text-decoration: none"><b>Funky Firewater</b></a>
    </div>
    <div class="nav-wrapper grey lighten-2">
        <ul class="left">
            <!--<li v-if="!isLoggedIn"><router-link to= "/login">Login</router-link></li>-->
            <li v-if="isLoggedIn"><button v-on:click="logout" class="btn-small grey lighten-1 black-text" style="margin-left: 15px">Logout</button></li>
            <li><router-link to= "/register" class="btn-small grey lighten-1 black-text" style="margin-left: 37px; text-decoration: none">Register</router-link></li>
            <router-view :key="$route.fullPath"></router-view>
        </ul>
        <ul class="right">
            <li v-if="isLoggedIn"><router-link to= "/" class="btn-small grey lighten-1 black-text" style="margin-right: 15px; text-decoration: none">Disable</router-link></li>
        </ul>
        <!--<ul class="left">
            <li v-if="isLoggedIn"><span class="email black-text">{{currentUser}}</span></li>
        </ul>-->
    </div>
  </nav>
    <!--<nav>
        <div class="nav-wrapper orange">
            <div class="container">
                <router-link to="/" class="brand-logo center"><b>Funky Firewater</b></router-link>
                <ul class="right">-->
                    <!--<li v-if="isLoggedIn"><span class="email black-text">{{currentUser}}</span></li>-->
                    <!--<li v-if="isLoggedIn"><router-link to= "/" class="btn-small orange darken-2" style="margin-right: -5px">Disable</router-link></li>-->
                    <!--<li v-if="!isLoggedIn"><router-link to= "/register">Register</router-link></li>-->
                <!--</ul>
                <ul class="left">
                    <li v-if="!isLoggedIn"><router-link to= "/login">Login</router-link></li>
                    <li v-if="isLoggedIn"><button v-on:click="logout" class="btn-small orange darken-2" style="margin-left: -5px">Logout</button></li>
                </ul>
            </div>
        </div>
    </nav>-->
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'navbar',
    data() {
        return {
            isLoggedIn: false,
            currentUser: false
        }
    },
    created() {
        if(firebase.auth().currentUser) {
            this.isLoggedIn = true;
            this.currentUser = firebase.auth().currentUser.email
        }
    },
    methods: {
        logout: function() {
            firebase.auth().signOut()
            .then(() => {
                javascript:history.go(0)
                alert('You have been successfully logged out.')
                this.$router.push('/login')
                this.$router.go({ path: this.$router.path})
                javascript:history.go(0)
            })
        }
    }
}
</script>

<style scoped>
    .brand-logo {
        font-size: 25px;
    }
</style>




